.info_section{
    grid-column: 1;
    grid-row: 1/3;
    height: 100%;
    border-right: 2px solid rgb(225,227,250);
    background-color: rgb(250,251,253);
    display: flex;
    flex-direction: column;
    gap: 15px;
}
.table_elem{
    display: flex;
    justify-content: space-between;
    width: 200px;
    max-width: 500px;
    gap: 10px;
}
.allow_tables{
    display: flex;
    flex-direction: column;
    gap: 15px;
    height: 100vh;
    align-items: center;
    justify-content: flex-start;
}


.h_elem{
    text-align: center;
    margin: 20px;
}